.tabs{
    font-family:arial;
    margin:0 auto;
  }
  
  
  
  .tab-buttons{
    margin-bottom:50px;
    padding-top: 150px;
  }
  
  button{
    margin-right:15px;
    font-size:20px;
    background:transparent;
    border:none;
    outline:none;
    padding:10px 20px ;
    cursor:pointer;
    color:rgba(0,0,0,.4);
    transition:all ease-in-out .2s;
    border-bottom:2px solid transparent;
    
  }
  .active{
    border-bottom:2px solid #42b3f4;
   
    color:#fff;
  }

  button:focus {
    outline: none;
}